/* Reset some default styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Style the navigation links */
.navbar-nav .nav-link {
    color: #333; /* Text color for links */
    font-weight: bold;
    display:flex;
    justify-content: left-right; /* Make links bold */
    transition: color 0.3s; /* Add a smooth color transition on hover */
}

/* Change link color on hover */
.navbar-nav .nav-link:hover {
    color: #ff9900; /* Change link color to a different color on hover */
}

/* Style the dropdown menu */
.dropdown-menu {
    background-color: #fff; /* Background color for the dropdown menu */
}

/* Style the form elements */
.form-control {
    border-radius: 0; 
}
/* Style the search button */
.btn-outline-success {
    color: #fff; /* Text color for the button */
    background-color: #28a745; /* Background color for the button */
    border-color: #28a745; /* Border color for the button */
    transition: background-color 0.3s, border-color 0.3s, color 0.3s; /* Add smooth transitions */
}

/* Change button styles on hover */
.btn-outline-success:hover {
    background-color: #218838; /* Change background color on hover */
    border-color: #1e7e34; /* Change border color on hover */
}
